.App {
  height: 100vh;
  background-image: url(/public/background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.App .background {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}

.App span {
  font-size: 10rem;
  color: white;
  text-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  z-index: 1;
}

.App .header {
  font-size: 3rem;
  color: white;
  text-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  z-index: 1;
  max-width: 50%;
  margin: 2rem;
}

.App .text {
  font-size: 2rem;
  color: white;
  text-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  z-index: 1;
  max-width: 50%;
  margin-bottom: 2rem;
  text-align: center;
}

.App .footer {
  font-size: 2rem;
  color: white;
  text-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  z-index: 1;
  max-width: 50%;
}

@media only screen and (max-width: 768px) {
  .App span {
    font-size: 3rem;
  }

  .App .header {
    font-size: 1.5rem;
    margin: 1.5rem;
  }

  .App .text {
    font-size: 1rem;
    margin-bottom: 1rem;
  }

  .App .footer {
    font-size: 1rem;
  }
}
